const initialState = {
	us: { filters: [], items: [] },
	la: { filters: [], items: [] },
	nyc: { filters: [], items: [] },
	denver: { filters: [], items: [] }
};
const my_areas = (state = initialState, action) => {
	switch (action.type) {
		case "my_area_item_add":
			/*
				requires:
				action.area_key,
				action.items
			*/
			var newItems = state[action.area_key].items.concat(action.items);
			// mutable array
			state[action.area_key].items = newItems;
			return Object.assign({}, state);
		default:
			return state;
	}
};

export default my_areas;



